<template>
        <section>
            <div class="my-popups">
                <div class="modal d-block ">
                    <div class="modal-dialog modal-dialog-centered ui-draggable">
                        <div class="modal-content">
                            <div class="modal-header ui-draggable-handle  py-0">
                                <h4 name="header" class="modal-title mt-2 pt-1">
                                    {{ $t('Comment') }} 
                                </h4>
                                <button class="close" v-on:click="closePopup"><em aria-hidden="true" class="fa fa-times"></em>
                                </button>
                            </div>
                        <div class="listing py-3 px-3">
                                <div id="CommentsData" class="listing" style="max-height: 465px;overflow-y: auto;">
                                <div id="accordion" class="w-100 activity-tab">
                                    <div class="card" v-for="(item,index) in CommentList">
                                        <div class="card-header">
                                            <a class="card-link" data-toggle="collapse" :href="'PostCommentListing'+index">
                                                <div class="conversation-date heading-border"><span>{{ $options.filters.formatDate(item.CREATED_AT) }}</span>
                                                </div>
                                            </a>
                                        </div>
                                        <div :id="'PostCommentListing'+index" class="collapse show" data-parent="#accordion">
                                            <div class="card-body">
                                                <div class="conversation-main" >
                                                    <div class="conversation-time"><span>{{ FormatDateToTime(item.CREATED_AT) }}</span></div>
                                                    <div class="conversation-box-con">
                                                        <div class="conversation-box">
                                                            <div class="name-circle light-yellow">
                                                                <img class="img-circle avatar"
                                                                    :src="'https://cogniterblob.blob.core.windows.net/cg-rohit-enterprises-122628/Upload/UserProfileImage/'+item.AVATAR"
                                                                    width="51" height="51" alt="Image">
                                                            </div>
                                                            <div class="conversation-header addednote-ticket">
                                                                <span>
                                                                    <span class="sender-name"></span>
                                                                    <span class="sender-department" :title="item.comments">
                                                                        {{ item.TRACK_ENTRY_COMMENT_DESC }}</span>
                                                                </span>
                                                            </div>
                                                            <div class="conversation-content">
                                                                <div class="col-12 p-0 notesdata">
                                                                    <h6 class="text-muted time"><i><b>{{ item.CREATED_BY }}</b>,
                                                                            Posted on: <span class="text-danger"
                                                                                title="11/28/2023 05:31:25 PM UTC">{{ DateDifference(item.CREATED_AT) }}</span> </i></h6>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>                           
                        </div> 

                    </div>
                </div>
            </div>
        </div>
     </section>  
</template>

<script>
import DataService from '../../../services/DataService';
import moment from 'moment';

export default {
    props: {
        selectedRecordValues: {
            type: Array,
            required: true
        },
        AssignDpt: {
            
            type: Array,
            required: true
        },
        TimeSheetDate:{
            type: String
        }
    },
    data() {
        return {
            weekDateRange:'',
            isLoading:false,
            CommentList:[],
          //  isAddAssignDept:false
        }
    },
    created: function () {
        this.FetchData();
    },
    methods: {
        async FetchData() {
            debugger;
            var vueObj = this;
            vueObj.isLoading = true;
            //long companyId, long SearchuserId, string userType, DateTime? dtFrom, DateTime? dtTo, long? userId, string showData, string statusCode, string sortBy, string sortExp, int pageSize, int? pageNum, long? projectId
            var url = `weekDateRange=${vueObj.TimeSheetDate}`
            await DataService.GetCommentListByWeekDateRange(url).then(response => {
                debugger;
                if (response.data.length > 0) {
                    vueObj.CommentList = response.data;
                    vueObj.isLoading = false;
                    // vueObj.TotalRecords = response.data.data[0].TotalRecords;
                    // vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                    // vueObj.CurrentPage = vueObj.pageNum;
                    vueObj.noRecord = false;
                    $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                }
                else {
                    vueObj.ApplicationCategoryHistoryList = [];
                }
                vueObj.isLoading = false;
            });
            setTimeout(function () {
                vueObj.CheckBoxBootstrap();
            }, 100);
        },
           closePopup() {
            var vm = this;
            vm.$parent.CloseComment();
        },
        FormatDateToTime(Getdate) {
            debugger
            return moment(Getdate).format('LT');
        },
        DateDifference(ActualDate){
            var vm = this;
            var GetCurrentDate = new Date();
            var GetValueInSeconds = Math.ceil(Math.floor(GetCurrentDate.getTime() - new Date(ActualDate).getTime())/1000);
            // TO Get Hours
            var hours = Math.floor(GetValueInSeconds / (60 * 60));
            // To Get Minutes
            var divisor_for_minutes = GetValueInSeconds % (60 * 60);
            var minutes = Math.floor(divisor_for_minutes / 60);
            var days = Math.floor(GetValueInSeconds / (3600*24));
            if(GetValueInSeconds < 60){
                return vm.$t('FewSecondsAgo');
            }else if (GetValueInSeconds < 120){
                return vm.$t('minutesago');
            }else if (GetValueInSeconds < 2700){
                return minutes + ' ' + vm.$t('minutesago');
            }else if (GetValueInSeconds < 5400){
                return vm.$t('an') + ' ' + vm.$t('hourago');
            }else if (GetValueInSeconds < 86400){
                return hours + ' ' + vm.$t('hourago');
            }else if (GetValueInSeconds < 172800){
                return vm.$t('Yesterday');
            }else if (GetValueInSeconds < 2592000){
                return days + ' ' + vm.$t('daysago');
            }else if (GetValueInSeconds < 31104000){
                var months = Math.floor(days / 30);
                return months <= 1 ? '1 ' + vm.$t('monthsago') : months + ' ' + vm.$t('monthsago')
            }else{
                var year = Math.floor(days/365);
                return year <= 1 ? '1 ' + vm.$t('yearsago') : year + ' ' + vm.$t('yearsago');
            }
        }
        }
}
</script>
