<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle  py-0">
                            <h4 name="header" class="modal-title mt-2 pt-1">
                                {{ $t('RollBack ') }}
                            </h4>
                            <button class="close" v-on:click="closePopup"><em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>
                    <div class="listing py-3 px-3">
                        <dynamic-form lang="en" ref="PostCommentForm" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                            <div class="py-2 mandatory">{{$t('MandatoryString')}}
            </div>
             <!-- Get Comments Listing -->
             <div id="CommentsData" class="listing" style="max-height: 465px;overflow-y: auto;">
                                <div id="accordion" class="w-100 activity-tab">
                                    <div class="card" v-for="(item,index) in PostedCommentListing">
                                        <div class="card-header">
                                            <a class="card-link" data-toggle="collapse" :href="'PostCommentListing'+index">
                                                <div class="conversation-date heading-border"><span>{{ $options.filters.formatDate(item.createdDate) }}</span>
                                                </div>
                                            </a>
                                        </div>
                                        <div :id="'PostCommentListing'+index" class="collapse show" data-parent="#accordion">
                                            <div class="card-body">
                                                <div class="conversation-main" >
                                                    <div class="conversation-time"><span>{{ FormatDateToTime(item.createdDate) }}</span></div>
                                                    <div class="conversation-box-con">
                                                        <div class="conversation-box">
                                                            <div class="name-circle light-yellow">
                                                                <img class="img-circle avatar"
                                                                    :src="'https://cogniterblob.blob.core.windows.net/cg-rohit-enterprises-122628/Upload/UserProfileImage/'+item.userImage"
                                                                    width="51" height="51" alt="Image">
                                                            </div>
                                                            <div class="conversation-header addednote-ticket">
                                                                <span>
                                                                    <span class="sender-name"></span>
                                                                    <span class="sender-department" :title="item.comments">
                                                                        {{ item.comments }}</span>
                                                                </span>
                                                            </div>
                                                            <div class="conversation-content">
                                                                <div class="col-12 p-0 notesdata">
                                                                    <h6 class="text-muted time"><i><b>{{ item.createdBy }}</b>,
                                                                            Posted on: <span class="text-danger"
                                                                                title="11/28/2023 05:31:25 PM UTC">{{ DateDifference(item.createdDate) }}</span> </i></h6>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>  
                    </div>
                    
                </div>
           
            
           
            </div>
        </div>
    </div>
 </section>  
</template>

<script>
//import DataService from '../../services/DataService';

export default {
props: {
    selectedRecordValues: {
        type: Array,
        required: true
    },
    AssignDpt: {
        
        type: Array,
        required: true
    }
},
data() {
    return {
        FormSchema: [{
                layoutType: "double",
                Data: [{
                            astype: "SelectField",
                            label: this.$t('Module'),
                            name: "Module",
                            value: 2,
                            placeholder: "",
                            config: {
                                options: [
                                    { value: 1, name: "Pending" },
                                    { value: 2, name: "Approved" },
                                    { value: 3, name: "Rejected" }],

                                onChange: this.OnDeploymentType,

                            },

                            // validationRules: { "required": true },

                        },
                    
                ]
            },
            {
                layoutType: "single",
            Data: [{
                astype: "TextAreaField",
                label: this.$t('ReasonforRollback'),
                name: "ReasonforRollback",
                value: "",
                mode: "tag",
                placeholder: "",
                config: {
                    options: [],
                },
                validationRules: {
                    "required": true
                },
            }]
            }
        ],
        buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-send pr-2'></i>Post New Comment",
                isVisible: true,
            },
        ],
        // DataLoaded: 0,
        // TagsSelectedArray: [],
        // departmentList: [],
        // newDepartmentlist: [],
        // ListData: [],
        // TagsSelectedArray1: [],
        // tagsData: [],
        // TagsSelectedArray2: [],
        // TagsSelectedArray3: [],
        // MultiArray: [],
        // obj: '',
        isLoading:false,
      //  isAddAssignDept:false
    }
},
created: function () {
   
},
methods: {
       closePopup() {
        var vm = this;
        vm.$parent.CloseRollBack();
    },
    }
}
</script>
