import DataService  from '../../services/DataService'
let buttons =
[
    {
        title: 'Back to list',
        iconClass: 'fa fa-angle-double-left pr-2',
        callbackfunction: 'BackTolist',
        additionalClass: '',
        isdisabled: false,
        isvisible: true,
        checkPrivilege: false
    },   
];
let leftsearchSchema = [
    {
        fieldName: 'Status',
        fieldType: "ddl-check",
        fieldIcon: "fa fa-bar-chart",
        paramName:"statusIds",
        fieldSearchConditionName: [''],
        isSearch: false,
        value:"",
        listOptions: [
            {value:'1001',name:'Approved'},
        {value:'1002',name:'Rejected'}
        ]
    },
    {
        fieldName: "Date From",
        fieldType: "date",
        fieldIcon: "fa-calendar",
        paramName:"dateFrom",
        fieldSearchConditionName: [''],
        isSearch: false,
        value: "",
        listOptions: []
    },
    {
        fieldName: "Date To",
        fieldType: "date",
        fieldIcon: "fa-calendar",
        paramName:"dateTo",
        fieldSearchConditionName: [''],
        isSearch: false,
        value: "",
        listOptions: []
    },
    
{
    fieldName: 'User',
    fieldType: "Radio",
    fieldIcon: "fa fa-users",
    paramName:"userIds",
    fieldSearchConditionName: [''],
    isSearch: false,
    value:"",
    listOptions: []
},]
function  bindleftCommonSearchdropdown(instance, filtername) {
    debugger;
    var vm = instance;
    if(filtername == 'CA'){
        DataService.GetUsersByCompanyId().then(response=>{
            response.data.forEach(item=>{
            let obj1={
                name:item.username,
                value:item.user_id
                }
                vm.leftSearchFields[3].listOptions.push(obj1);
            })
        }).catch(err =>{
            console.log(err);
        })
    }
 }

export default { buttons, leftsearchSchema,bindleftCommonSearchdropdown};